require('dotenv').config()
const {
  VUE_APP_POSTS_PER_PAGE
} = process.env;

import Vue from 'vue'
import VueMeta from 'vue-meta'
import BootstrapVue from 'bootstrap-vue'
import Vuex from 'vuex'
import Router from 'vue-router'
import App from './App.vue'
import axios from 'axios'

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import 'github-markdown-css/github-markdown.css'

import ForkeMeOnGithub from 'fork-me-on-github-vue';

import Home from '@/views/Home'
import Post from '@/views/Post'

// Fetch index file
const fetchPostsIndex = async() => {
  return (await axios.get('/data/posts_index.json')).data
}

const loadApp = async() => {
  Vue.config.productionTip = false
  Vue.use(Router)
  Vue.use(VueMeta)
  Vue.use(BootstrapVue)
  Vue.use(Vuex)
  Vue.use(ForkeMeOnGithub)

  const postsIndex = await fetchPostsIndex();
  const store = new Vuex.Store({
    state: {
      postsIndex,
      VUE_APP_POSTS_PER_PAGE
    }
  });
  
  const router = new Router({
    mode: 'history',
    routes: [
      {
        path: '/:language?',
        name: 'index',
        component: Home,
        props: true
      },
      {
        path: '/:language/sections/:section?',
        name: 'home',
        component: Home,
        props: true,
      }, {
        path: '/:language/sections/:section/:id',
        name: 'post',
        component: Post,
        props: true,
      }
    ]
  });

  new Vue({
    metaInfo: {
      title: 'Home',
      meta:  [
        { charset: 'utf-8' },
        { name: 'viewport', content: 'width=device-width,initial-scale=1.0,minimum-scale=1.0,maximum-scale=1.0,user-scalable=no' },
        { name: 'robots', content: 'index, follow' },
        { name: 'twitter:card', content: 'summary_large_image' },
        { name: 'twitter:site', content: 'raphaelbarbosaqwerty, github' },
        { name: 'description', content: 'Blog about information security, programming, studies.' },
        { name: 'og:title', content: 'raphaelbarbosaqwerty | Reports' },
        { property: 'og:type', content: 'profile' },
        { name: 'keywords', content: 'raphaelbarbosaqwerty, hacking, hackthebox, tryhackme, pentesting, study, infosec, security-information, cyberpunk, programming, learning, brain' },
        { property:"twitter:image", content:"https://raphaelbarbosaqwerty.github.io/data/assets/limitless.jpg" },
        { property:"og:image", content:"https://raphaelbarbosaqwerty.github.io/data/assets/limitless.jpg" }
      ],
    },
    router,
    store,
    render: h => h(App)
  }).$mount('#app')
}

loadApp();
